.profile {
  width: 428px;
  height: 504px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.description {
  background-color: #fff;
  width: 252px;
  height: 250px;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px 0;
}

.avatar {
  border-radius: 50px;
}

.name {
  color: #122236;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  margin: 0;
  padding: 0;
  margin-top: 28px;
}

.tag {
  color: #768696;
  font-size: 14px;
  line-height: 32px;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.location {
  color: #768696;
  font-size: 14px;
  line-height: 32px;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.stats {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 76px;
  width: 252px;
  background-color: #f3f6f9;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stats li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 84px;
  height: 100%;
}

.stats li:not(:last-child) {
  border-right: 1px solid #e4e9f0;
}

.lable {
  font-size: 12px;
  line-height: 32px;
  color: #768696;
  font-weight: 400;
}

.quantity {
  font-size: 14px;
  line-height: 32px;
  color: #1f3349;
  font-weight: 900;
}
